// BedCard.jsx (React Component Snippet)

import React, { useState } from 'react';
import axios from 'axios'; // We use axios for simpler API calls

const API_BASE_URL = 'http://localhost:3000/api/v1';

const BedCard = ({ bedData, onStatusChange, token }) => {
    const [status, setStatus] = useState(bedData.current_status);
    const bedId = bedData.bed_id;
    
    // --- Styles for the Card ---
    const getStatusColor = (currentStatus) => {
        switch (currentStatus) {
            case 'AVAILABLE': return 'bg-green-100 text-green-800';
            case 'OCCUPIED': return 'bg-red-100 text-red-800';
            case 'CLEANING': return 'bg-yellow-100 text-yellow-800';
            default: return 'bg-gray-100 text-gray-800';
        }
    };

    // --- Core Logic: Status Update ---
    const handleStatusUpdate = async (newStatus) => {
        if (!window.confirm(`Are you sure you want to change Bed ${bedId} status to ${newStatus}?`)) {
            return;
        }

        try {
            // Replace 'YOUR_JWT_TOKEN' with the actual token received on login (MVP-401)
            // const token = 'YOUR_JWT_TOKEN'; 

            await axios.post(`${API_BASE_URL}/beds/${bedId}/status`, 
                { new_status: newStatus },
                {
                    headers: {
                        'Authorization': `Bearer ${token}` 
                    }
                }
            );
            
            // On success, update the local state to reflect the change
            setStatus(newStatus); 
            alert(`Status updated to ${newStatus}`);
            onStatusChange();

        } catch (error) {
            console.error('Error updating status:', error.response ? error.response.data : error.message);
            alert('Failed to update status. Check console for details.');
        }
    };
    
    // --- Rendering the Component ---
    return (
        <div className={`p-4 border rounded-lg shadow-md ${getStatusColor(status)}`}>
            <h3 className="font-bold text-lg mb-1">Bed ID: {bedId}</h3>
            <p>Ward: {bedData.ward_name}</p>
            <p>Specialty: {bedData.specialty_type}</p>
            <p className="mt-2 text-xl font-extrabold">Status: {status}</p>
            
            <div className="mt-3 space-x-2">
                {status !== 'AVAILABLE' && (
                    <button 
                        className="bg-green-500 text-white p-1 rounded text-sm"
                        onClick={() => handleStatusUpdate('AVAILABLE')}>
                        Mark Available
                    </button>
                )}
                {status !== 'OCCUPIED' && (
                    <button 
                        className="bg-red-500 text-white p-1 rounded text-sm"
                        onClick={() => handleStatusUpdate('OCCUPIED')}>
                        Mark Occupied
                    </button>
                )}
                {/* ... other status buttons (e.g., CLEANING) */}
            </div>
        </div>
    );
};

export default BedCard;