// frontend/src/pages/DashboardPage.jsx

import React, { useState, useEffect, useCallback } from 'react';
import axios from 'axios';
import BedCard from '../components/BedCard'; // Import the BedCard component
impor
// Assuming we have a dedicated service for API calls
// import { fetchBeds } from '../api/bedService'; 

// --- Configuration ---
const API_BASE_URL = 'http://localhost:3000/api/v1';

// Hardcoded for MVP, should be fetched from a central config/database later
const SPECIALTY_OPTIONS = [
    'All', 'General', 'ICU', 'Pediatric', 'HDU', 
    'CCU', 'NICU', 'Emergency beds', 'PACU', 'PICU'
];

const DashboardPage = () => {
    const [beds, setBeds] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState(null);
    const [selectedSpecialty, setSelectedSpecialty] = useState('All');
    
    // NOTE: This should come from local storage after successful login (MVP-401)
    const token = localStorage.getItem('authToken') || 'YOUR_JWT_TOKEN'; 

    // --- 1. Fetch Data Function (MVP-101 & MVP-103) ---
    const fetchBedData = useCallback(async (specialty) => {
        setIsLoading(true);
        setError(null);
        
        try {
            // Construct the API URL with an optional query parameter for filtering
            let url = `${API_BASE_URL}/beds`;
            if (specialty && specialty !== 'All') {
                url += `?specialty_type=${specialty}`;
            }

            const response = await axios.get(url, {
                headers: {
                    // Send the JWT token for authentication (Auth Middleware check)
                    'Authorization': `Bearer ${token}` 
                }
            });
            
            setBeds(response.data);

        } catch (err) {
            console.error('Failed to fetch bed data:', err);
            // Handle 401 Unauthorized (Token expired/invalid)
            if (err.response && err.response.status === 401) {
                setError("Session expired or unauthorized. Please log in.");
                // Redirect to login page in a production app
            } else {
                 setError('Could not load bed data. Please check the API server.');
            }
        } finally {
            setIsLoading(false);
        }
    }, [token]);


    // --- Effect Hook: Runs on component mount and when selectedSpecialty changes ---
    useEffect(() => {
        fetchBedData(selectedSpecialty);
    }, [selectedSpecialty, fetchBedData]);


    // --- 2. Filter Handler ---
    const handleFilterChange = (event) => {
        setSelectedSpecialty(event.target.value);
    };

    // --- 3. Display Logic ---
    if (isLoading) return <div className="text-center p-8">Loading bed status...</div>;
    if (error) return <div className="text-center p-8 text-red-600 font-bold">{error}</div>;

    return (
        <div className="p-6 max-w-7xl mx-auto">
            <h1 className="text-3xl font-bold mb-6 text-indigo-700">üè• Hospital Bed Status Dashboard</h1>
            
            {/* Filter Component (MVP-103) */}
            <div className="mb-6 flex items-center space-x-3">
                <label htmlFor="specialty-filter" className="font-semibold">Filter by Specialty:</label>
                <select 
                    id="specialty-filter"
                    value={selectedSpecialty}
                    onChange={handleFilterChange}
                    className="p-2 border rounded-md shadow-sm"
                >
                    {SPECIALTY_OPTIONS.map(specialty => (
                        <option key={specialty} value={specialty}>
                            {specialty}
                        </option>
                    ))}
                </select>
                <span className="text-sm text-gray-500">
                    Displaying {beds.length} beds.
                </span>
            </div>

            {/* Bed List Grid (MVP-101)  */}
            {beds.length > 0 ? (
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
                    {beds.map(bed => (
                        // Pass a callback function to BedCard so it can trigger a dashboard refresh after a successful update
                        <BedCard 
                            key={bed.bed_id} 
                            bedData={bed} 
                            onStatusChange={() => fetchBedData(selectedSpecialty)}
                            token={token}
                        />
                    ))}
                </div>
            ) : (
                <div className="text-center p-10 bg-yellow-50 rounded-lg">
                    No beds found for the selected specialty.
                </div>
            )}
        </div>
    );
};

export default DashboardPage;